body {
  margin: 0 auto;
  padding: 0;
  font-family: "PTMono", "Arial", sans-serif; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.mobile-only {
  display: none; }

.tablet-only {
  display: none; }

.desktop-only {
  display: block; }

@media (max-width: 1023px) {
